.modalKick {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
}

h1 {
	text-align: center;
}

.modalKick {
	display: flex;
	flex-grow: 1;
	flex-direction: column;
	gap: 20px;
	align-items: center;
	justify-content: center;
}

.btn {
	width: 200px;
	height: 50px;
	border-radius: 20px;
	border: 0;
}

.kickBtns {
	display: flex;
	gap: 20px;
}

/* Ширина не учитывая высоту */
@media (max-width: 1120px) {
	.btn {
		width: 120px;
		height: 50px;
	}
	
	.kickBtns {
		gap: 10px;
	}
}
